import React from 'react'
import PropTypes from 'prop-types'
import InspectionItem from './InspectionItem'

function InspectionList({ inspection }) {
  return (
    <div
      className="
        border-t
        border-x
        border-gray-300
        rounded-lg
        bg-white
        dark:bg-gray-800
      "
    >
      {
        inspection.map((e) => (
          <InspectionItem
            key={e.id_ramp_inspection}
            inspection={e}
          />
        ))
      }
    </div>
  )
}
InspectionList.propTypes = {
  inspection: PropTypes.oneOfType([PropTypes.array]),
}
InspectionList.defaultProps = {
  inspection: () => [],
}
export default InspectionList
