import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import FloatingBtn from '../components/global/FloatingBtn'
import InspectionList from '../components/inspection/InspectionList'
import UserTemplate from '../template/UserTemplate'

function User() {
  const inspection = useSelector((state) => state.rampInspection.rampInspection)
  const [inspectionFiltered, setInspectionFiltered] = useState(inspection)

  const [filter, setFilter] = useState({})

  const filterRampInspection = () => {
    let filtered = inspection
    if ('q' in filter && filter.q !== '') {
      filtered = filtered.filter((e) => e.aircraft_model.toLowerCase().includes(filter.q)
      || e.operator.toLowerCase().includes(filter.q))
    }
    setInspectionFiltered(filtered)
  }

  const handleSearch = (e) => {
    const search = e.target.value
    setFilter((values) => ({ ...values, q: search }))
  }

  useEffect(() => {
    filterRampInspection()
  }, [filter])

  return (
    <UserTemplate>
      <div className="px-4 pt-16">
        {/* header */}
        <div className="text-3xl text-gray-700 text-center">
          <h1>Ramp Inspection Checklist</h1>
        </div>
        {/* <div className="mt-8 p-4 bg-brand-100  border border-brand-200 rounded-lg">
          <div>
            <span className="block mt-4">Jumlah data</span>
            <span
              className="block font-medium text-brand-600"
            >
              3
            </span>
          </div>

        </div> */}

        <div
          className="mt-16"
        >
          <input
            type="text"
            placeholder="Search"
            className="t-form"
            value={filter.q || ''}
            onChange={handleSearch}
            required
          />
        </div>
        <div
          className="mt-4"
        >
          <InspectionList
            inspection={inspectionFiltered}
          />
        </div>
        <Link
          to="/create"
          className="fixed z-100 bottom-24 right-4"
        >
          <FloatingBtn />
        </Link>
        <div style={{ height: '100vh' }} />
      </div>
    </UserTemplate>
  )
}

export default User
